.card {
  display: flex;
  flex-direction: column;
  width: 200px;
  background-color: var(--color-white);
  border: 1px solid #f3f3f3;
  padding: 32px 16px 16px;
}
  
.card__image {
  width: 160px;
  height: 134px;
  margin: 0 3px;
}
  
.card__name {
  font-weight: 500;
  font-size: 12px;
  line-height: 18px;
  color: var(--color-dark);
  margin-top: 40px;
}
  
.card__product-code {
  font-size: 10px;
  line-height: 14px;
  color: var(--color-light-grey);
  margin-top: 4px;
}
  
.card__price {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin-top: 24px;
}
  
.card__price-text {
  font-size: 12px;
  line-height: var(--price-line-height);
  color: var(--color-light-grey);
}
  
.card__price-value {
  font-weight: 700;
  font-size: 16px;
  line-height: var(--price-line-height);
  color: var(--color-dark);
}
  
.card__link-buy {
  text-align: center;
  text-transform: uppercase;
  background-color: var(--color-blue);
  border-radius: 5px;
  text-decoration: none;
  color: var(--color-white);
  font-weight: 700;
  font-size: 14px;
  line-height: 16px;
  padding: 12px 70px 12px 69px;
  margin-top: 16px;
}
  
.card__link-buy:hover {
  background-color: var(--color-white);
  border: 1px solid var(--color-blue);
  border-radius: 5px;
  color: var(--color-blue);
}
